.menu {
  width: 245px;
  height: 100%;
  background-color: $background-default;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
  z-index: 4;
}

.menu__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-bottom: 20px;
}

.menu .navigation {
  display: block;
  margin: 0;
}

.menu .navigation__list {
  display: flex;
  flex-direction: column;
}

.menu .navigation__item {
  min-height: 50px;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
}

.menu .navigation__item--active {
  background-color: $background-decorative;
}

.menu .navigation__item--active .navigation__link {
  color: $text-inverse;
}

.menu .navigation__link {
  font-size: 18px;
  line-height: 23px;
  color: $text-default;
  width: 100%;
  height: 100%;
  display: flex;
}

.menu .phone {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.menu .phone__icon {
  margin-right: 10px;
}

.menu .phone__number {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: $text-default;
}

.menu .container {
  width: 100%;
}

.menu .address {
  display: flex;
  align-items: center;
}

.menu .address__icon {
  margin-right: 10px;
}

.menu .address__text {
  color: $text-default;
}

.menu__info {
  margin-top: auto;
}
